import axios from "axios";
import config from "../../config.json";
import CUrl from "../../constants/CowinUrl.json";
import moment from "moment";

function GetCenters2 (url, callback) {
    axios.get(url, {
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        }})
        .then( response => {
            if(response.status === 200 && response.data.centers) {
                callback(response.data.centers);
            }
        }).catch( err => {
            console.error(err);
        });
}

function GetCenters (url, callback) {
    fetch(url)
        .then(response => response.json())
        .then(data => callback(data.centers));
}

function CheckForTheCenters (pincode, callback) {
    console.log("getting details for pin code: " + pincode);
    let  date = moment().format("DD-MM-YYYY");
    GetCenters(config.COWIN_BASE_URL + CUrl.CALENDAR_BY_PIN_CODE.replace("<PINCODE>", pincode).replace("<DATE>", date).replace("<VACCINE>", ""), callback);
}

export {CheckForTheCenters, GetCenters};